.heart, .share{
  display: flex;
  position: relative;
  width: 50px;
  height: 50px;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.heart .fa-solid, .share .fa-solid{
  font-size: 25px;
  -webkit-text-stroke: 2px #ff5e00;
  color: transparent;
}

.heart::before, .share::before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  overflow: hidden;
  border-bottom: 3px solid #ff5e00;
  transition: 0.5s ease-in-out;
  font-family: fontAwesome;
  text-align: center;
  line-height: 50px;
  font-size: 25px;
  color: #ff5e00;
}

.share:hover::before, .heart:hover::before{
  height: 100%;
}

.share::before{
  content: '\f064';
}

.heart::before{
  content: '\f004';
}

.iframe-h{
  height: 500px;
}


@media screen and (max-width: 991px){
  .iframe-h{
    height: 200px;
  }
}

@media screen and (max-width: 767px){
  .scroller{
    overflow-x: auto;
    scroll-behavior: smooth;
  }
  .scroller::-webkit-scrollbar{
    display: none;
  }
}