.alert-box{
  width: 300px;
  min-height: 125px;
  border-radius: 10px;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
  padding: 20px;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
}

.alert-img{
  position: absolute;
  width: 60px;
  top: -20%;
  left: 50%;
  transform: translateX(-50%);
}

.alert-msg{
  font-size: 20px;
  font-weight: 500;
}

.success{
  color: #0ab50a;
}

.unsuccess{
  color: #ff5a5a;
}