/* product listing */

.no-video{
  display: none;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.no-video-txt{
  display: block;
  margin: 40px 0;
  text-transform: capitalize;
  color: #3f3d56;
}

.thumbnail{
  width: 250px;
}

/* action buttons */
.card-action-btn{
  position: absolute;
  top: 10px;
  right: 10px;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  border: none;
  outline: none;
  transition: .5s;
  background: #fff;
  overflow: hidden;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
}

.card-action-btn img{
  height: 18px;
}

.open-btn{
  right: 60px;
}

.delete-popup-btn{
  right: 110px;
  background: #f25252;
}

.hide{
  display: none;
}

/* delete alert */
.delete-alert{
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.2);
  z-index: 2;
  display: none;
  justify-content: center;
  align-items: center;
}

.alert{
  width: 400px;
  height: auto;
  border-radius: 10px;
  background: #fff;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  position: relative;
  padding: 15px;
}

.close-btn{
  position: absolute;
  top: 15px;
  right: 15px;
  background: none;
  border: none;
  cursor: pointer;
}

.close-btn i{
  font-size: 18px;
}

.delete-svg{
  height: 60px;
  display: block;
  margin: 20px auto;
}

.text{
  color: #000;
  text-transform: capitalize;
  text-align: center;
}

.delete-btn{
  cursor: pointer;
  border: none;
  border-radius: 50px;
  background: #f25252;
  margin: 15px auto 0;
  display: block;
  padding: 10px 20px;
  text-transform: capitalize;
  color: #fff;
}