.header{
  position: relative;
  text-align: center;
  color: #fff;
}

.header::before{
  content: "";
  position: fixed;
  background: url(../img/bg.png) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  width: 100vw;
  height: 100vh;
  left: 0;
  z-index: -1;
}

.cont{
  position: relative;
  /* top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding-top: 500px; */
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.heading{
  font-size: 65px;
  font-weight: 600;
  margin-bottom: 40px;
}

.search-box{
  width: 85%;
  border-bottom-left-radius: 50px;
  border-top-left-radius: 50px;
  flex: 1;
  background-color: #e5e5e5;
  border: none;
  outline: none;
  padding: 0 20px;
  font-size: 18px;
}

.search-btn{
  width: 15%;
  background: #ff5e00;
  padding: 10px 0;
  border: none;
  border-bottom-right-radius: 50px;
  border-top-right-radius: 50px;
  cursor: pointer; 
}

.search-icon{
  width: 30px;
  height: auto;
}

.category-list{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
}

.category-list a{
  text-decoration: none;
  color: #fff;
}

.category{
  width: 110px;
  height: 100px;
  background: rgba(255, 94, 0, 0.5);
  border-radius: 15px;
  margin: 0 10px;
  font-size: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transition: 0.5s;
  cursor: pointer;
}

.category:hover{
  background: rgba(255, 94, 0, 1);
}

.category i{
  font-size: 25px;
  margin-bottom: 7px;
}