*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins;
}

::-webkit-scrollbar{
  width: 10px;
}

::-webkit-scrollbar-thumb{
  background: #a0a0a0;
  border-radius: 50px;
  transition: .5s;
}

::-webkit-scrollbar-thumb:hover{
  background: #ff8640;
}

::-webkit-scrollbar-track{
  background: transparent;
}

body{
  background: #fff;
}

.back-ground, .back-ground-dark{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
  background-size: 300% 300%;
}

.back-ground-dark{
  opacity: 0.5;
}

.navbar{
  --bs-navbar-toggler-focus-width: 0;
}

.navbar-links{
  padding: 0 10px;
  text-decoration: none;
}

.nav-item{
  border-bottom: none;
}

.active{
  color: blue !important;
  font-weight: 500;
}

.navbar-btn{
  border: none;
  font-size: 25px !important;
}

.offcanvas-lg{
  width: 100%;
}

.form-check-input:checked{
  border-color: #bfbfbf;
}

.form-check-input:focus {
  border-color: #bfbfbf;
  box-shadow: none;
}

.form-switch .form-check-input:focus{
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='rgba%280, 0, 0, 0.25%29'/%3e%3c/svg%3e");
}

.form-switch .form-check-input:checked:focus{
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%23fff'/%3e%3c/svg%3e");
}

.shadow-top{
  box-shadow: -1px 0 10px rgba(0, 0, 0, 0.25);
}

.form-input{
  width: 50%;
}
.forgot-link{
  width: 75%;
}

@media screen and (max-width: 991px){
  #input{
    height: 200px;
  }
  .fa-moon{
    position: absolute;
    left: 0;
  }
   .nav-item{
    border-bottom: 1px solid #000;
  }
  .container{
    border-radius: 0;
  }
  .form-input{
    width: 75%;
  }
  .forgot-link{
    width: 90%;
  }
  .bg-logo{
    position: relative !important;
    padding: 0 0.5rem;
  }
}

@media screen and (max-width: 425px){
  .offcanvas-lg{
    width: 85% !important;
  }
}

.cursor{
  cursor: pointer;
}

.bg-color{
  background: #ff5e00 !important;
  color: #fff !important;
}

.myBtn{
  font-size: 15px;
  font-weight: 500;
  border: none;
  border-radius: 50px;
  text-transform: capitalize;
  cursor: pointer;
}

.bg-form{
  background: #e5e5e5;
}

.f-12{
  font-size: 12px;
}

.f-14{
  font-size: 14px;
}

.bg-logo{
  background: url(./img/Logo-png.png) center 1px no-repeat;
  background-size: 80px;
}

.fav-icon{
  -webkit-text-stroke: 2px #f00;
  color: transparent;
  transition: .5s;
}

.fav-icon:hover{
  color: #f00;
}

.filter1, .filter2, .filter3, .filter4, .filter5, .filter6{
  list-style: none;
  background: #eee;
  padding: 12px 30px;
  margin: 5px;
  border-radius: 50px;
  cursor: pointer;
  width: auto;
}